import noconetnt from '@/components/noconetnt.vue'
export default {
	data() {
		return {
			id:'',
			week:['星期一','星期二','星期三','星期四','星期五','星期六','星期天'],
			list:[]
		};
	},
	onLoad(o){
		this.id = o.id;
	},
	onShow() {
		this.getinfo();
	},
	methods:{
		getinfo(){
			uni.request({
				url: this.$api+'/weixin/shishenxin/myclassdemx',
				data: this.getmd5({
					openId:uni.getStorageSync('openId'),
					courseId:this.id,
				}),
				header: {
					'content-type': 'application/json'
				},
				method: "POST",
				success: (res) => {
					if (res.data.code != 200) {
						uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
						});
					} else {
						uni.hideLoading();
						var data = res.data.data;
						this.list = data
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
					icon: 'none',
					title: '获取失败',
					duration: 2000
					});
				}
			});
		},
	},
	components:{
		noconetnt
	}
}
